<template>
	<v-container>
		<v-dialog v-model="dialog_newMonth" persistent max-width="1000px">
			<v-card>
				<v-card-title>
					<span class="headline"> Nuevo Mes</span>
				</v-card-title>
				<v-card-text>
					<v-form ref="form" v-model="valid" lazy-validation>
						<v-container>
							<v-row>
								<v-col cols="12" md="4">
									<v-select
										:items="dataPeriodos"
										item-text="periodo"
										item-value="periodo"
										label="Periodo"
										v-model="periodo"
										dense
									>
										<template v-slot:append-outer>
											<v-tooltip bottom>
												<template v-slot:activator="{ on }">
													<v-icon v-on="on">
													mdi-help-circle-outline
													</v-icon>
												</template>
												Seleccione el mes correspondiente a los datos que ingresar.
											</v-tooltip>
										</template>
									</v-select>
								</v-col>
								<v-col cols="12" md="4">
									<v-select
										:items="dataProyectos"
										item-text="proyecto_pais"
										item-value="id"
										label="Proyecto"
										v-model="proyecto"
										dense
									>
										<template v-slot:append-outer>
											<v-tooltip bottom>
												<template v-slot:activator="{ on }">
													<v-icon v-on="on">
													mdi-help-circle-outline
													</v-icon>
												</template>
												Seleccione el proyecto al que corresponden los datos que va a ingresar.
											</v-tooltip>
										</template>
									</v-select>
								</v-col>
								<v-col cols="12" md="4">
									<v-btn color="primary" dark class="mb-2" @click="buscar()">
										Buscar
									</v-btn>
								</v-col>
							</v-row>				
						</v-container>
					</v-form>
					<component :is="newMonthComponent" :data="dataHrsLaborMonth" :nextPeriodo="nextPeriodo" ref="ListNewMonth" @showMessage="showMessage" 
						@close="close"></component>
				</v-card-text>
				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn color="blue darken-1" text @click="close()">Cancelar</v-btn>
					<v-btn color="blue darken-1" text @click="save()" :disabled="disabledSave" >Guardar</v-btn>
				</v-card-actions>
			</v-card>
		</v-dialog>
		<v-snackbar
      v-model="snackbar"
      :timeout="timeout"
    >
      {{ text }}
      <template v-slot:action="{ attrs }">
        <v-btn
          color="red"
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
	</v-container>
</template>
<script>
	import ListNewMonth from '@/components/HrsLabor/ListNewMonth'
  import http from '@/plugins/axios'
  import swal from 'sweetalert'
	import { mapState } from 'vuex'
	
  export default {
    name: 'EditHrsLabor',
		components: {
			ListNewMonth
		},
    data: () => ({
			disabledSave: true,
			newMonthComponent: null,
			valid: true,
			itemsServicios: [],
			dataHrsLaborMonth: {},
			nextPeriodo: null,
			proyecto: null,
			periodo: null,
			snackbar: false,
			text: '',
			timeout: 3000
    }),
    methods: {
			showMessage(msg){
				this.snackbar = true
				this.text = msg
			},
      close() {
        this.$store.commit('updateShowNewMonth')
				this.newMonthComponent = null
				this.nextPeriodo = null
				this.dataHrsLaborMonth = {}
      },
			getNextPeriodo(periodo){
				let months  = {'ENE': 0,'FEB': 1,'MAR': 2,'ABR': 3,'MAY': 4,'JUN': 5,'JUL': 6,'AGO': 7,'SEP': 8,'OCT': 9,'NOV': 10,'DIC': 11}
				let months2 = {0: 'ENE',1: 'FEB',2: 'MAR',3: 'ABR',4: 'MAY',5: 'JUN',6: 'JUL',7: 'AGO',8: 'SEP',9: 'OCT',10: 'NOV',11: 'DIC'}
				let actualPeriodo = periodo.split(" -- ")
				let t = new Date(actualPeriodo[1], months[actualPeriodo[0]], 1)
				t.setMonth(t.getMonth() + 1 )
				let nextPeriodo = `${months2[t.getMonth()]} -- ${t.getFullYear()}`
				return nextPeriodo
			},
			buscar(){
				this.newMonthComponent = null
				if (this.periodo!=null && this.proyecto != null){
					let nextPeriodo = this.getNextPeriodo(this.periodo)
					let next = this.dataPeriodos.find(e => e.periodo === nextPeriodo)
					if (next !== undefined) {
						http.post("api/list_hrs_labor_new_month", {'proyecto': this.proyecto, 'periodo': this.periodo})
						.then((response) => {
							if (response.data.code){
								this.disabledSave = true
								this.showMessage(response.data.msg)
							}else{
								let data = response.data
								this.disabledSave = false
								this.newMonthComponent = ListNewMonth
								this.dataHrsLaborMonth = data
								this.nextPeriodo = next
							}
						})
						.catch(()=>{
							this.disabledSave = true
							swal("Error Interno", "Favor contactar con el administrador", "error");
						})
					}else{
						this.disabledSave = true
						swal("Error", `Favor contactar con el administrador y solicitar que cree el nuevo periodo ${nextPeriodo}`, "error");
					}
				}else{
					this.showMessage('Seleccione un periodo y un proyecto')
				}
			},   
      save(){
				this.$refs.ListNewMonth.saveMonth()
      },
    },
    computed: {
			...mapState(['dialog_newMonth', 'dataPeriodos', 'dataProyectos']),
		},
  };
</script>

<style >
	.my-input input{ text-transform: uppercase}
</style>