<template>
  <v-container>
      <v-data-table
        :headers="headers"
        :search="search"
				:items="data"
      >
        <template v-slot:[`item.linea_base_horas`]="{ item }">
          <v-edit-dialog
            :return-value.sync="item.linea_base_horas"
          >
            {{ item.linea_base_horas }}
            <template v-slot:input>
              <v-text-field
                v-model="item.linea_base_horas"
                type="number"
                min="0"
                label="Ingrese el valor y Enter"
                single-line
                counter
              ></v-text-field>
            </template>
          </v-edit-dialog>
        </template>
        <template v-slot:[`item.horas_ejecutadas`]="{ item }">
          <v-edit-dialog
            :return-value.sync="item.horas_ejecutadas"
          >
            {{ item.horas_ejecutadas }}
            <template v-slot:input>
              <v-text-field
                v-model="item.horas_ejecutadas"
                type="number"
                min="0"
                label="Ingrese el valor"
                single-line
                counter
              ></v-text-field>
            </template>
          </v-edit-dialog>
        </template>
        <template v-slot:[`item.actions`]="{ item }">
          <v-icon
            small
            @click="deleteItem(item)"
          >
            mdi-delete
          </v-icon>
        </template> 
      </v-data-table>
  </v-container>
</template>

<script>
  import swal from 'sweetalert'
  import http from '@/plugins/axios'
  import { mapState } from 'vuex'

  export default {
    name: 'HrsLabor',
		props: {
			data: {
				type: Array,
				default: () => {}, 
			},
      nextPeriodo: {
        type: Object,
				default: () => {},
      }
		},
    data: () => ({
      search: null,
      headers: [
        { "text": "Periodo", "value": "periodo.periodo" },
        { "text": "Pais", "value": "pais"},
        { "text": "Proyecto", "value": "proyecto.proyecto"},
        { "text": "Servicio", "value": "servicio" },
        { "text": "Linea Base Hrs", "value": "linea_base_horas"},
        { "text": "Horas ejecutadas", "value": "horas_ejecutadas" },
        // { "text": '% Uso', "value": 'uso'},
        { "text": 'Actions', "value": 'actions', "sortable": false },
      ],
    }),
    created() {
      this.data.forEach((e) => {
        delete e.id
        delete e.periodo
        e.periodo = {'id': this.nextPeriodo.id, 'periodo': this.nextPeriodo.periodo}
        e.linea_base_horas = null
        e.horas_ejecutadas = null
        e.uso = null
      })
    },
    computed: {
      ...mapState(['permisos', 'dataPeriodos'])
    },
    methods: {
      calcularUso(){
        this.data.forEach((e) => {
          if(e.linea_base_horas !== 0){
            e.uso = parseFloat(e.horas_ejecutadas / e.linea_base_horas * 100).toFixed(2)
          }else{
            e.uso = 0
          }
        })
      },
      post(url){
        http.post(url, this.data)
        .then((response) => {
          let data = response.data
          console.log(data)
          data.forEach((e) => {
            this.$store.commit("newHrsLabor", e)
          })
          swal("Creado Correctamente!", {
            icon: "success",
          });
        })
        .catch(() => {
          console.log("error")
          swal("Error Interno", "Favor contactar con el administrador", "error");
        });
      },
      saveMonth() {
        if (this.data.length > 0){
          let isValid = true
          for (let i = 0; i < this.data.length; i++) {          
            if ( this.data[i].linea_base_horas === null || this.data[i].horas_ejecutadas === null 
                || this.data[i].linea_base_horas === "" || this.data[i].horas_ejecutadas === ""
                || parseFloat(this.data[i].linea_base_horas) <= 0 || parseFloat(this.data[i].horas_ejecutadas) < 0
                ){
              isValid = false
              break;
            }
          }
          if (isValid){
            this.calcularUso()
            this.post("api/hrs_labor_new_month")
            this.$emit('close')
          }else{
            this.$emit('showMessage',"Para cada registro, Ingrese la Linea Base Hrs > 0 y Horas ejecutadas >= 0")
          }
        }else{
          this.$emit('showMessage',"Eliminaste todos los registros, favor buscar nuevamente")
        }
      },
      deleteItem(item) {
        let index = this.data.indexOf(item)
        this.data.splice(index, 1)
      }
    }
  }
</script>

